import React from 'react'
import NavBar from '../components/NavBar'

import Link from 'next/link'

class BookPage extends React.Component {
    render() {
        return (
            <div className="container">
                <NavBar />
                <h3 className="text-center">Some Books!</h3>
                <div class="jumbotron">
                    <div class="over container body-content">
                        <h1 class="display-5">A curated directory of books for shit kunts to help you along your python programming peregrination</h1>
                        <p class="lead">hahah</p>
                    </div>
                </div>
                <Link href="/index"><button className="justify-content-end btn btn-warning">Back</button></Link>
            </div>
        )
    }
}

export default BookPage